@font-face {
  font-family: 'Clear Sans';
  src:
    local('Clear Sans Regular'),
    local('ClearSans'),
    url('../fonts/ClearSans.woff2') format('woff2'),
    url('../fonts/ClearSans.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Clear Sans';
  src:
    local('Clear Sans Bold'),
    local('ClearSans-Bold'),
    url('../fonts/ClearSans-Bold.woff2') format('woff2'),
    url('../fonts/ClearSans-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
